import { Component, OnInit } from '@angular/core'
import { Evidence } from './game-data/evidence'
import { EvidenceState } from './game-data/evidence-state'
import { ProofState } from './game-data/proof-state'
import { Ghost } from './game-data/ghost'
import { GHOSTS } from './game-data/GHOSTS'

@Component({
  selector: 'my-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  evidenceStates: EvidenceState[]
  possibleGhosts: Ghost[]
  bestBets: string[] = []

  ngOnInit(): void {
    this.evidenceStates = this.createEvidenceStates()
    this.possibleGhosts = GHOSTS
  }

  updateEvidence(updatedEvidenceState: EvidenceState) {
    this.evidenceStates = this.evidenceStates.map((state) => {
      return state.evidence === updatedEvidenceState.evidence
        ? updatedEvidenceState
        : state
    })
    this.calculatePossibleGhosts()
    this.calculateNextSteps()
  }

  private calculatePossibleGhosts() {
    const proofedEvidences = this.evidenceStates
      .filter((evidence) => evidence.state === ProofState.PROOFED)
      .map((evidenceState) => evidenceState.evidence)
    const proofedGhosts = GHOSTS.filter((ghost) =>
      proofedEvidences.every((evidence) => ghost.evidences.includes(evidence))
    )

    const excludedEvidences = this.evidenceStates
      .filter((evidence) => evidence.state === ProofState.NEVER)
      .map((evidenceState) => evidenceState.evidence)

    const possibleGhosts = proofedGhosts.filter(
      (ghost) =>
        !excludedEvidences.some((evidence) =>
          ghost.evidences.includes(evidence)
        )
    )
    this.possibleGhosts = possibleGhosts
  }

  private calculateNextSteps() {
    if (
      this.possibleGhosts.length === 1 ||
      this.possibleGhosts.length === GHOSTS.length
    ) {
      this.bestBets = []
    } else {
      const unknownEvidences = this.evidenceStates
        .filter((evidence) => evidence.state === ProofState.DONT_KNOW)
        .map((evidenceState) => evidenceState.evidence)
      this.bestBets = unknownEvidences.filter((unknownEvidence) => {
        let evidenceUsedForGhosts = 0
        this.possibleGhosts.forEach((ghost) => {
          if (ghost.evidences.includes(unknownEvidence)) {
            evidenceUsedForGhosts++
          }
        })
        return evidenceUsedForGhosts === 1
      })
    }
  }

  private createEvidenceStates(): EvidenceState[] {
    return Object.keys(Evidence).map((evidenceKey) => ({
      evidence: Evidence[evidenceKey],
      state: ProofState.DONT_KNOW,
    }))
  }
}
