import { Component, EventEmitter, Input, Output } from '@angular/core'
import { Evidence } from '../game-data/evidence'
import { EvidenceState } from '../game-data/evidence-state'
import { ProofState } from '../game-data/proof-state'

@Component({
  selector: 'evidence-state',
  templateUrl: './evidence-state.component.html',
  styleUrls: ['./evidence-state.component.scss'],
})
export class EvidenceStateComponent {
  @Input() state: EvidenceState
  @Input() name: string
  @Output() evidenceChanged: EventEmitter<EvidenceState> = new EventEmitter()

  proofStateOptions = Object.keys(ProofState).map(proofStateKey => ({
    key: proofStateKey,
    name: ProofState[proofStateKey],
  }));

  updateEvidence(proofStateKey: string) {
    this.state = { ...this.state, state: ProofState[proofStateKey] }
    this.evidenceChanged.emit(this.state)
  }

  isSelected(proofStateKey: string) {
    return ProofState[proofStateKey] === this.state.state
  }
}
